export default class Localization {

    private static s_instance: Localization = null;

    // 
    private m_data: LocalizationDefineType = null;

    public static getInstance(): Localization {
        if (this.s_instance === null) {
            this.s_instance = new Localization();
        }
        return this.s_instance;
    }

    public static destroy(): void {
        if (this.s_instance !== null) {
            this.s_instance.destroy();
        }
        this.s_instance = null;
    }

    constructor() {

    }

    public set data(value: LocalizationDefineType) {
        this.m_data = value;
    }

    /**
     * 
     * @param key {LocalizationDefineType}  key
     * @param format {string[]} 
     * @return {string}  value
     */
    public get(key: LocalizationDefineType, ...format: string[] | number[]): string {
        let value: string = "";
        if (this.m_data && this.m_data[key] !== undefined) {
            value = this.m_data[key];
        }

        if (format.length > 0) {
            value = value.replace(/{(\d+)}/g, (_: string, matchIndex: string) => {
                let index: number = Number(matchIndex);
                let content: string | number | undefined = format[index];
                let result: string = "";
                if (content === undefined) {
                    result = "?"
                } else {
                    if (typeof (content) === "number") {
                        result = content.toString();
                    } else {
                        result = content;
                    }
                }
                return result;
            });
        }
        
        return value;
    }

    /**
     * 
     */
    public destroy(): void {
        this.m_data = null;
    }
}