import ViewBase from "../../ViewBase";

const { ccclass, property } = cc._decorator;

// 
const MOVE_PIXEL: number = 50;

@ccclass
export default class TestView extends ViewBase implements PersistInterface {

    @property({ type: cc.Node, tooltip: "" })
    private nodRoot: cc.Node = null;

    @property({ type: cc.Label, tooltip: "" })
    private labSwitch: cc.Label = null;

    @property({ type: cc.Button, tooltip: "" })
    private btnSwitch: cc.Button = null;

    // 
    private m_switchBtnOriginPos: cc.Vec2 = null;
    // 
    private m_isOpen: boolean = null;

    protected onLoad(): void {
        this.register();
        this.initData();
        this.initView();
    }

    protected start(): void {

    }

    /**
     * 
     */
    private register(): void {
        this.btnSwitch.node.on(cc.Node.EventType.TOUCH_START, this.onSwitchTouchStart, this);
        this.btnSwitch.node.on(cc.Node.EventType.TOUCH_MOVE, this.onSwitchTouchMove, this);
        this.btnSwitch.node.on(cc.Node.EventType.TOUCH_END, this.onSwitchTouchEnd, this);
        this.btnSwitch.node.on(cc.Node.EventType.TOUCH_CANCEL, this.onSwitchTouchCancel, this);
    }

    /**
     * 
     */
    private unRegister(): void {

    }

    /**
     * 
     */
    private initData(): void {
        this.m_isOpen = false;
    }

    /**
     * 
     */
    private initView(): void {
        this.setSwitch(this.m_isOpen);
    }

    /**
     * 
     * @param isOpen {boolean} 
     */
    private setSwitch(isOpen: boolean): void {
        let strSwitch: string = "";
        if (isOpen) {
            strSwitch = "";
        } else {
            strSwitch = "";
        }
        this.labSwitch.string = strSwitch;
        this.nodRoot.active = isOpen;
    }

    /**
     * 
     */
    public open(): void {

    }

    /**
     * 
     */
    public close(): void {

    }

    /**
     *  
     */
    private onSwitchTouchStart(event: cc.Event.EventTouch): void {
        this.m_switchBtnOriginPos = this.btnSwitch.node.getPosition();
    }

    /**
     *  
     */
    private onSwitchTouchMove(event: cc.Event.EventTouch): void {
        let startLocation: cc.Vec2 = event.getStartLocation();
        let moveLocation: cc.Vec2 = event.getLocation();

        if (Math.abs(moveLocation.x - startLocation.x) < MOVE_PIXEL && Math.abs(moveLocation.y - startLocation.y) < MOVE_PIXEL) {
            return;
        }
        this.btnSwitch.interactable = false;
        let movePos: cc.Vec2 = this.btnSwitch.node.parent.convertToNodeSpaceAR(moveLocation);
        this.btnSwitch.node.setPosition(movePos);
    }

    /**
     *  
     */
    private onSwitchTouchEnd(event: cc.Event.EventTouch): void {
        this.btnSwitch.interactable = true;
        let node: cc.Node = event.target;
        this.m_switchBtnOriginPos = node.getPosition();
    }

    /**
     *  
     */
    private onSwitchTouchCancel(event: cc.Event.EventTouch): void {
        this.btnSwitch.interactable = true;
        let node: cc.Node = event.target;
        node.setPosition(this.m_switchBtnOriginPos);
    }

    /**
     * 
     */
    private onClickSwitch(): void {
        this.m_isOpen = !this.m_isOpen;
        this.setSwitch(this.m_isOpen);
    }

    /**
     * 
     */
    private onClickOpenTips(): void {
        let timestamp: number = new Date().getTime();
        let content: string = timestamp.toString();;
        G.UIMgr.openTips(content);
    }

    /**
     * 
     */
    protected onDestroy(): void {
        this.unRegister();
    }

}