export default class TimeUtil {

    /**
     * 
     * @param timestamp 
     * @return {number} | 
     */
    public static markTime(timestamp?: number): number {
        let currTime: number = performance.now();
        if (timestamp === undefined) {
            // 
            return currTime;
        } else {
            // 
            return currTime - timestamp;
        }
    }
}