import Logger from "../core/machine/Logger";

export default class Util {

    /**
     * 
     * @param value {number | string} 
     * @param digit {number}  2 
     * @return {number}
     */
    public static toFixed(value: number | string, digit: number = 2): number {
        if (digit <= 0) {
            Logger.getInstance().warn(`bit ${digit}`);
            return null;
        }

        let num: number = Number(value)
        if (value === null || value === undefined || isNaN(num)) {
            return null;
        }

        let multiple: number = 1;
        for (let i: number = 0; i < digit; ++i) {
            multiple *= 10;
        }

        let result: number = Math.floor(num * multiple) / multiple;
        return result;
    }

    /**
     *   
     * @param bytesSize {number}  2 
     * @return {string} 
     */
    public static bytesToFileUnit(bytesSize: number): string {
        // 
        const CARRY: number = 1024;
        // 
        const SYMBOLS: string[] = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
        let value: number = bytesSize;
        let unit: string = SYMBOLS[0];
        let index: number = 0;
        while (index < (SYMBOLS.length - 1) && (value / CARRY) >= 1) {
            value /= CARRY;
            unit = SYMBOLS[++index];
        }

        return `${Util.toFixed(value)} ${unit}`;
    }
}