import md5 from "../lib/md5";

export default class CodeUtil {

    /**
     * Uint8Array  string
     * @param uint8Array {Uint8Array} 
     * @return {string} 
     */
    public static uint8ArrayToString(uint8Array: Uint8Array): string {
        /**
         * Chrome  TextDecoder Native  
         */
        // return new TextDecoder("utf-8").decode(uint8Array);

        let str: string = "";
        let index: number = 0;
        let char1: number = 0;
        let char2: number = 0;
        let char3: number = 0;

        while (index < uint8Array.length) {
            char1 = uint8Array[index++];
            switch (char1 >> 4) {
                case 0:
                case 1:
                case 2:
                case 3:
                case 4:
                case 5:
                case 6:
                case 7:
                    str += String.fromCharCode(char1);
                    break;
                case 12:
                case 13:
                    char2 = uint8Array[index++];
                    str += String.fromCharCode(((char1 & 0x1F) << 6) | (char2 & 0x3F));
                    break;
                case 14:
                    char2 = uint8Array[index++];
                    char3 = uint8Array[index++];
                    str += String.fromCharCode(((char1 & 0x0F) << 12) | ((char2 & 0x3F) << 6) | ((char3 & 0x3F) << 0));
                    break;
            }
        }

        return str;
    }


    /**
     * string  Uint8Array
     * @param str {string} 
     * @return {Uint8Array} 
     */
    public static stringToUint8Array(str: string): Uint8Array {
        /**
         * Chrome  TextEncoder Native  
         */
        // return new TextEncoder().encode(str);

        let pos: number = 0;
        const strLen: number = str.length;
        let at: number = 0;
        let tlen: number = Math.max(32, strLen + (strLen >> 1) + 7);
        let target: Uint8Array = new Uint8Array((tlen >> 3) << 3);

        while (pos < strLen) {
            let value: number = str.charCodeAt(pos++);
            if (value >= 0xd800 && value <= 0xdbff) {
                if (pos < strLen) {
                    const extra: number = str.charCodeAt(pos);
                    if ((extra & 0xfc00) === 0xdc00) {
                        ++pos;
                        value = ((value & 0x3ff) << 10) + (extra & 0x3ff) + 0x10000;
                    }
                }
                if (value >= 0xd800 && value <= 0xdbff) {
                    continue;
                }
            }

            if (at + 4 > target.length) {
                tlen += 8;
                tlen *= (1.0 + (pos / strLen) * 2);
                tlen = (tlen >> 3) << 3;
                const update: Uint8Array = new Uint8Array(tlen);
                update.set(target);
                target = update;
            }

            if ((value & 0xffffff80) === 0) {
                target[at++] = value;
                continue;
            } else if ((value & 0xfffff800) === 0) {
                target[at++] = ((value >> 6) & 0x1f) | 0xc0;
            } else if ((value & 0xffff0000) === 0) {
                target[at++] = ((value >> 12) & 0x0f) | 0xe0;
                target[at++] = ((value >> 6) & 0x3f) | 0x80;
            } else if ((value & 0xffe00000) === 0) {
                target[at++] = ((value >> 18) & 0x07) | 0xf0;
                target[at++] = ((value >> 12) & 0x3f) | 0x80;
                target[at++] = ((value >> 6) & 0x3f) | 0x80;
            } else {
                continue;
            }

            target[at++] = (value & 0x3f) | 0x80;
        }

        return target.slice(0, at);
    }

    /**
     * Uint8Array  Array
     * @param uint8Array {Uint8Array} 
     * @return {number[]}
     */
    public static uint8ArrayToArray(uint8Array: Uint8Array): number[] {
        return Array.prototype.slice.call(uint8Array);
    }

    /**
     * MD5 
     * @param data {string | any[] | Uint8Array | ArrayBuffer} 
     * @return {string} 
     */
    public static md5(data: string | any[] | Uint8Array | ArrayBuffer): string {
        return md5(data);
    }

}