import LocalStorageDefine from "../../define/LocalStorageDefine";
import ViewDefine from "../../define/ViewDefine";
import SceneBase from "../SceneBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class AccountScene extends SceneBase {

    // 
    private m_token: string | null = null;

    protected onLoad(): void {
        this.register();
        this.initData();
        this.initView();
    }

    protected start(): void {
        if(this.m_token) {
            console.log(` token: ${this.m_token}`);
        } else {
            G.UIMgr.openView(ViewDefine.LoginView);
        }
    }

    /**
     * 
     */
    private register(): void {

    }

    /**
     * 
     */
    private unRegister(): void {

    }

    /**
     * 
     */
    private initData(): void {
        this.m_token = cc.sys.localStorage.getItem(LocalStorageDefine.LOCAL_LOGIN_TOKEN);
    }

    /**
     * 
     */
    private initView(): void {

    }

    /**
     * 
     */
    protected onDestroy(): void {
        this.unRegister();
    }

}