import * as HttpInterface from "../../interface/HttpInterface";
import * as HttpDefine from "../../define/HttpDefine";
import HttpXmlRequest from "./HttpXmlRequest";

export default class HttpRequest {

    /**
     *  URL 
     * @param url {string} 
     * @return {boolean}
     */
    private static checkLegal(url: string): boolean {
        let legal: boolean = true;
        if (url === null || url === undefined || url === "") {
            legal = false;
        }
        return legal;
    }

    /**
     * GET 
     * @param url {string} 
     * @param responseType {XMLHttpRequestResponseType} 
     * @param cls {T extends HttpInterface.Method} 
     * @return {Promise<HttpInterface.ResponseInfo>}  .then 
     */
    public static async get<T extends HttpInterface.Method>(url: string, responseType?: XMLHttpRequestResponseType, cls?: (new () => T)): Promise<HttpInterface.ResponseInfo> {
        if (!this.checkLegal(url)) {
            console.warn(`GET  ${url}`);
            return;
        }

        let response: HttpInterface.ResponseInfo;
        if (cls === null || cls === undefined) {
            response = await (new HttpXmlRequest()).request(url, HttpDefine.Method.GET);
        } else {
            response = await (new cls()).request(url, HttpDefine.Method.GET);
        }

        return response;
    }

    /**
     * POST 
     * @param url {string} 
     * @param body {string} JSON 
     * @param responseType {XMLHttpRequestResponseType} 
     * @param cls {T extends HttpInterface.Method} 
     * @return {Promise<HttpInterface.ResponseInfo>}  .then  .catch
     */
    public static async post<T extends HttpInterface.Method>(url: string, body?: string, responseType?: XMLHttpRequestResponseType, cls?: (new () => T)): Promise<HttpInterface.ResponseInfo> {
        if (!this.checkLegal(url)) {
            console.warn(`POST  ${url}`);
            return;
        }

        let response: HttpInterface.ResponseInfo;
        if (cls === null || cls === undefined) {
            response = await (new HttpXmlRequest()).request(url, HttpDefine.Method.POST, body);
        } else {
            response = await (new cls()).request(url, HttpDefine.Method.POST, body);
        }
        return response;
    }
}