import Manager from "../Manager";
import AudioDefine from "../../../define/AudioDefine";
import Loader from "../../machine/Loader";
import Audio from "./Audio";
import Pool from "../../../pattern/Pool";
import Logger from "../../machine/Logger";
import AudioEffectUtil from "../../../utils/AudioEffectUtil";

// 
const MAX_SAME_TIME_PLAY_SIZE = 10;
// 
const GRADUALLY_TIME = 1;

export default class AudioManager extends Manager implements ManagerInterface {

    private static s_instance: AudioManager = null;

    // 
    private m_soundPool: Pool<Audio> = null;
    // 
    private m_soundMap: Map<AudioDefineType, Audio> = null;
    // 
    private m_music: Audio = null;

    public static getInstance(): AudioManager {
        if (this.s_instance === null) {
            this.s_instance = new AudioManager();
        }
        return this.s_instance;
    }

    public static destroy(): void {
        if (this.s_instance !== null) {
            this.s_instance.destroy();
        }
        this.s_instance = null;
    }

    constructor() {
        super();

        this.m_soundPool = new Pool<Audio>(Audio, MAX_SAME_TIME_PLAY_SIZE);
        this.m_soundMap = new Map();
        this.m_music = new Audio();
    }

    /**
     * 
     * @param path {AudioDefineType} 
     * @return {boolean}
     */
    private checkLegal(path: AudioDefineType): boolean {
        let legal: boolean = true;
        if (path === null || path === undefined) {
            legal = false;
        }
        return legal;
    }

    /**
     *  
     * @param path {AudioDefineType} 
     * @param isGradually {boolean}  true
     */
    public playMusic(path: AudioDefineType, isGradually: boolean = true): void {
        Loader.getInstance().load(path, (clip: cc.AudioClip) => {
            if (clip === null) {
                return;
            }

            let playCallBack: Function = () => {
                this.m_music.clear();
                this.m_music.setClip(clip);
                this.m_music.setPath(path);
                this.m_music.regCallback(() => {
                    Loader.getInstance().unload(path);
                    this.m_music.clear();
                });
                if (isGradually) {
                    AudioEffectUtil.openGradually(this.m_music);
                } else {
                    this.m_music.play();
                }

                this.m_music.loop = true;
            }

            let preMusicPath: AudioDefineType = this.m_music.getPath() as AudioDefineType;
            if (preMusicPath !== null) {
                if (isGradually) {
                    AudioEffectUtil.closeGradually(this.m_music, 0, GRADUALLY_TIME, () => {
                        Loader.getInstance().unload(path);
                        playCallBack();
                    })
                } else {
                    Loader.getInstance().unload(path);
                    playCallBack();
                }
            } else {
                playCallBack();
            }
        });
    }

    /**
     * 
     */
    public pauseMusic(): void {
        if (!this.m_music.getPath() || !this.m_music.isPlaying) {
            Logger.getInstance().warn(` ${this.m_music.getPath()}`);
            return;
        }

        this.m_music.pause();
    }

    /**
     * 
     * @param isGradually {boolean} 
     */
    public stopMusic(isGradually: boolean = true): void {
        if (!this.m_music.getPath() || !this.m_music.isPlaying) {
            Logger.getInstance().warn(` ${this.m_music.getPath()}`);
            return;
        }

        if (isGradually) {
            AudioEffectUtil.closeGradually(this.m_music, 0, GRADUALLY_TIME, () => {
                this.m_music.stop();
            })
        } else {
            this.m_music.stop();
        }

    }

    /**
     * 
     */
    public resumeMusic(): void {
        if (!this.m_music.getPath() || this.m_music.isPlaying) {
            Logger.getInstance().warn(` ${this.m_music.getPath()}`);
            return;
        }

        this.m_music.resume();
    }

    /**
     *  
     * @param path {AudioDefineType} 
     * @param isBreak {boolean} true false 
     * @param isCache {boolean} true false 
     */
    public playSound(path: AudioDefineType, isBreak: boolean = false, isCache: boolean = true): void {
        if (isBreak) {
            let audio: Audio = this.m_soundMap.get(path);
            if (audio !== null && audio !== undefined) {
                audio.stop();
            }
        }

        Loader.getInstance().load(path, (clip: cc.AudioClip) => {
            if (clip === null) {
                return;
            }
            // 
            let audio: Audio = this.m_soundPool.get();
            this.m_soundMap.set(path, audio);
            audio.setClip(clip);
            audio.setPath(path);
            audio.regCallback(() => {
                let asset: cc.Asset = Loader.getInstance().getCache(path);
                if (!isCache || asset.refCount > 1) {
                    // 
                    Loader.getInstance().unload(path);
                }
                this.m_soundPool.put(audio);
                // 
                if (asset.refCount <= 0) {
                    this.m_soundMap.delete(path);
                }
            });
            // 
            audio.play();
            audio.loop = false;
        });
    }

    /**
     * 
     * @param path {AudioDefineType} 
     */
    public pauseSound(path: AudioDefineType): void {
        if (!this.checkLegal(path)) {
            Logger.getInstance().warn(` ${path}`);
            return;
        }

        let audio: Audio = this.m_soundMap.get(path);
        if(audio === null || audio === undefined) {
            Logger.getInstance().warn(` ${path}`);
            return;
        }

        if(!audio.isPlaying) {
            Logger.getInstance().warn(` ${path}`);
            return;
        }

        audio.pause();
    }

    /**
     * 
     */
    public pauseAllSound(): void {
        let soundSize: number = this.m_soundMap.size;
        if (soundSize > 0) {
            this.m_soundMap.forEach((value: Audio) => {
                value.pause();
            });
        }
    }

    /**
     * 
     * @param path {AudioDefineType} 
     */
    public stopSound(path: AudioDefineType): void {
        if (!this.checkLegal(path)) {
            Logger.getInstance().warn(` ${path}`);
            return;
        }

        let audio: Audio = this.m_soundMap.get(path);
        if(audio === null || audio === undefined) {
            Logger.getInstance().warn(` ${path}`);
            return;
        }

        if(!audio.isPlaying) {
            Logger.getInstance().warn(` ${path}`);
            return;
        }

        audio.stop();
    }

    /**
     * 
     */
    public stopAllSound(): void {
        let soundSize: number = this.m_soundMap.size;
        if (soundSize > 0) {
            this.m_soundMap.forEach((value: Audio) => {
                value.stop();
            });
        }
    }

    /**
     * 
     * @param path {AudioDefineType} 
     */
    public resumeSound(path: AudioDefineType): void {
        if (!this.checkLegal(path)) {
            Logger.getInstance().warn(` ${path}`);
            return;
        }

        let audio: Audio = this.m_soundMap.get(path);
        if(audio === null || audio === undefined) {
            Logger.getInstance().warn(` ${path}`);
            return;
        }

        if(audio.isPlaying) {
            Logger.getInstance().warn(` ${path}`);
            return;
        }

        audio.resume();
    }

    /**
     * 
     */
    public resumeAllSound(): void {
        let soundSize: number = this.m_soundMap.size;
        if (soundSize > 0) {
            this.m_soundMap.forEach((value: Audio) => {
                value.resume();
            });
        }
    }

    /**
     * 
     */
    public playClick(): void {
        this.playSound(AudioDefine.CLICK, false);
    }


    /**
     * 
     */
    private clearSound(): void {
        this.m_soundMap.forEach((value: Audio) => {
            value.stop();
            value.release();
        });
        this.m_soundMap.clear();
    }

    /**
     * 
     */
    private clearMusic(): void {
        this.m_music.stop();
        this.m_music.release();
    }

    /**
     *    destroy
     */
    public destroy(): void {
        this.clearSound();
        this.clearMusic();
        this.m_soundPool.destroy();
        this.m_soundPool = null;
        this.m_soundMap = null;
        this.m_music = null;
    }

}