import LanguageDefine from "./define/LanguageDefine";
import LocalStorageDefine from "./define/LocalStorageDefine";
import * as GameConfig from "./config/GameConfig"
import LanguageUtil from "./utils/LanguageUtil";
import Logger from "./core/machine/Logger";
import EventManager from "./core/manager/event/EventManager";
import { SystemEventDefine } from "./define/EventDefine";
import UIManager from "./core/manager/ui/UIManager";
import NetworkManager from "./core/manager/network/NetworkManager";
import SceneDefine from "./define/SceneDefine";
import * as NetworkDefine from "./define/NetworkDefine";
import Localization from "./i18n/Localization";
import LocalizationDefine from "./define/LocalizationDefine";

export default class Game {

    private static s_instance: Game = new Game();

    // 
    private m_language: LanguageDefineType = null;
    // 
    private m_vesrion: string = null;
    // 
    private m_musicVolume: number = null;
    // 
    private m_soundVolume: number = null;

    public static getInstance(): Game {
        if (this.s_instance === null) {
            this.s_instance = new Game();
        }
        return this.s_instance;
    }

    public static destroy(): void {
        if (this.s_instance !== null) {
            this.s_instance.destroy();
        }
        this.s_instance = null;
    }

    constructor() {
        this.register();
        this.initData();
    }

    /**
     * 
     */
    private register(): void {
        EventManager.getInstance().on(SystemEventDefine.WS_CONNECTING, this, this.onWebSocketConnecting);
        EventManager.getInstance().on(SystemEventDefine.WS_CONNECTED, this, this.onWebSocketConnected);
        EventManager.getInstance().on(SystemEventDefine.WS_ERROR, this, this.onWebSocketError);
        EventManager.getInstance().on(SystemEventDefine.WS_CLOSING, this, this.onWebSocketClosing);
        EventManager.getInstance().on(SystemEventDefine.WS_CLOSED, this, this.onWebSocketClosed);
        EventManager.getInstance().on(SystemEventDefine.WS_MESSAGE_TIMEOUT, this, this.onWebSocketMessageTimeout);
        EventManager.getInstance().on(SystemEventDefine.WS_MESSAGE_NORMAL, this, this.onWebSocketMessageNormal);
        EventManager.getInstance().on(SystemEventDefine.WS_PONG_TIMEOUT, this, this.onWebSocketPongTimeout);
        EventManager.getInstance().on(SystemEventDefine.HTTP_TIMEOUT, this, this.onHttpTimeout);
        EventManager.getInstance().on(SystemEventDefine.HTTP_ERROR, this, this.onHttpError);
        EventManager.getInstance().on(SystemEventDefine.HTTP_CODE_ERROR, this, this.onHttpCodeError);
    }

    /**
     * 
     */
    private initData(): void {
        this.initMusicVolume();
        this.initSoundVolume();
    }

    /**
     * 
     */
    private onWebSocketConnecting(): void {
        UIManager.getInstance().openLoading(`...`);
    }
    /**
     * 
     */
    private onWebSocketConnected(): void {
        UIManager.getInstance().closeLoading();

        // TODO: 
    }

    /**
     * 
     */
    private onWebSocketError(): void {

    }

    /**
     * 
     */
    private onWebSocketClosing(): void {
        UIManager.getInstance().openLockTouch();
    }

    /**
     * 
     * @param closeState {NetworkDefine.CloseState | null} 
     */
    private onWebSocketClosed(closeState: NetworkDefine.CloseState | null): void {
        UIManager.getInstance().closeLockTouch();

        switch (closeState) {
            case NetworkDefine.CloseState.ERROR_CLOSE:
                UIManager.getInstance().closeLoading();
                UIManager.getInstance().openPopups("...", "", null, () => {
                    UIManager.getInstance().openScene(SceneDefine.AccountScene);
                });
                break;
            default:
                UIManager.getInstance().openLoading(`...`);
                NetworkManager.getInstance().reconnect("ws://121.89.193.124:30000");
                break;
        }
    }

    /**
     * 
     */
    private onWebSocketMessageNormal(): void {
        UIManager.getInstance().closeLockTouch();
        UIManager.getInstance().closeLoading();
    }

    /**
     * 
     */
    private onWebSocketPongTimeout(): void {

    }
    
    /**
     * Http 
     */
    private onHttpTimeout(): void {
        UIManager.getInstance().openTips(Localization.getInstance().get(LocalizationDefine.HTTP_TIMEOUT));
    }

    /**
     * Http 
     */
    private onHttpError(): void {
        UIManager.getInstance().openTips(Localization.getInstance().get(LocalizationDefine.HTTP_ERROR));
    }

    /**
     * Http 
     */
    private onHttpCodeError(content: string): void {
        UIManager.getInstance().openTips(content);
    }

    /**
     * 
     */
    private onWebSocketMessageTimeout(): void {
        UIManager.getInstance().openLoading(`...`);
    }

    /**
     *  GameConfig  DEFAULT_MUSIC_VOLUME 
     */
    private initMusicVolume(): void {
        this.m_musicVolume = GameConfig.DEFAULT_MUSIC_VOLUME;
        let volume: string = cc.sys.localStorage.getItem(LocalStorageDefine.LOCAL_MUSIC_VOLUME);
        if (volume !== null && volume !== undefined && !isNaN(Number(volume)) && Number(volume) >= 0 && Number(volume) <= 1) {
            this.m_musicVolume = Number(volume);
        } else {
            cc.sys.localStorage.setItem(LocalStorageDefine.LOCAL_MUSIC_VOLUME, this.m_musicVolume.toString());
        }
    }

    /**
     *  GameConfig  DEFAULT_SOUND_VOLUME 
     */
    private initSoundVolume(): void {
        this.m_soundVolume = GameConfig.DEFAULT_SOUND_VOLUME;
        let volume: string = cc.sys.localStorage.getItem(LocalStorageDefine.LOCAL_SOUND_VOLUME);
        if (volume !== null && volume !== undefined && !isNaN(Number(volume)) && Number(volume) >= 0 && Number(volume) <= 1) {
            this.m_soundVolume = Number(volume);
        } else {
            cc.sys.localStorage.setItem(LocalStorageDefine.LOCAL_SOUND_VOLUME, this.m_soundVolume.toString());
        }

    }

    public get language(): LanguageDefineType {
        return this.m_language;
    }

    public set language(language: LanguageDefineType) {
        if (language !== null && language !== undefined && LanguageDefine[language] !== undefined) {
            this.m_language = language;
            cc.sys.localStorage.setItem(LocalStorageDefine.LOCAL_LANGUAGE, language);
        } else {
            Logger.getInstance().warn(`language  ${language}`);
        }
    }

    public get musicVolume(): number {
        return this.m_musicVolume;
    }

    public set musicVolume(volume: number) {
        if (volume !== null && volume !== undefined && !isNaN(Number(volume)) && Number(volume) >= 0 && Number(volume) <= 1) {
            this.m_musicVolume = volume;
            cc.sys.localStorage.setItem(LocalStorageDefine.LOCAL_MUSIC_VOLUME, volume.toString());
        } else {
            Logger.getInstance().warn(`musicVolume  ${volume}`);
        }
    }

    public get soundVolume(): number {
        return this.m_soundVolume;
    }

    public set soundVolume(volume: number) {
        if (volume !== null && volume !== undefined && !isNaN(Number(volume)) && Number(volume) >= 0 && Number(volume) <= 1) {
            this.m_soundVolume = volume;
            cc.sys.localStorage.setItem(LocalStorageDefine.LOCAL_SOUND_VOLUME, volume.toString());
        } else {
            Logger.getInstance().warn(`soundVolume  ${volume}`);
        }
    }

    public get vesrion(): string {
        return this.m_vesrion;
    }

    public set version(version: string) {
        if (version !== null && version !== undefined) {
            this.m_vesrion = version;
        } else {
            Logger.getInstance().warn(`version `);
        }
    }

    /**
     * 
     */
    public destroy(): void {
        this.m_language = null;
        this.m_musicVolume = null;
        this.m_soundVolume = null;
        this.m_vesrion = null;
    }
}