import * as UpdateDefine from "../../../define/UpdateDefine";
import * as UpdateInterface from "../../../interface/UpdateInterface";
import * as UpdateConfig from "../../../config/UpdateConfig";
import Manager from "../Manager";
import Logger from "../../machine/Logger";
import CodeUtil from "../../../utils/CodeUtil";
import LocalStorageDefine from "../../../define/LocalStorageDefine";
import Util from "../../../utils/Util";
import EventManager from "../event/EventManager";
import EventDefine from "../../../define/EventDefine";

// 
const NATIVE_UPDATE_ASSETS_FOLDER: string = "hot-update";
// 
const TEXT_FILE_SUFFIX: string[] = ["json"];
// 
const MAX_FAILED_FILE_COUNT: number = 3;
// project.manifest 
const PROJECT_MANIFEST_FILENAME: string = "project.manifest";

export default class UpdateManager extends Manager implements ManagerInterface {

    private static s_instance: UpdateManager = null;

    // 
    private m_jsbAssetsManager: jsb.AssetsManager = null;
    // 
    private m_diffVersionNum: UpdateDefine.VersionNumber = null;
    // 
    private m_failedFiles: string[] = null;
    // 
    private m_errorState: UpdateDefine.ErrorState = null;
    // 
    private m_percent: number = null;

    public static getInstance(): UpdateManager {
        if (this.s_instance === null) {
            this.s_instance = new UpdateManager();
        }
        return this.s_instance;
    }

    public static destroy(): void {
        if (this.s_instance !== null) {
            this.s_instance.destroy();
        }
        this.s_instance = null;
    }

    constructor() {
        super();

        this.initData();
    }

    /**
     * 
     */
    private initData(): void {
        this.m_diffVersionNum = null;
        this.m_failedFiles = [];
        this.m_errorState = null;
        this.m_percent = 0;
    }

    /**
     * 
     * @return {Promise<UpdateInterface.CheckResult>} 
     */
    public async check(): Promise<UpdateInterface.CheckResult> {
        if (!cc.sys.isNative) {
            return {
                state: UpdateDefine.CheckState.NOT,
            };
        }

        return new Promise((resolve: (result: UpdateInterface.CheckResult) => void) => {
            if (this.m_jsbAssetsManager === null) {
                this.m_jsbAssetsManager = jsb.AssetsManager.create(this.getLocalManifestPath(), this.getUpdateSearchPath());
                this.m_jsbAssetsManager.setVersionCompareHandle(this.onVersionCompare.bind(this));
                this.m_jsbAssetsManager.setVerifyCallback(this.onVerifyMD5.bind(this));
            }

            let jsbState: jsb.AssetsManager.State = this.m_jsbAssetsManager.getState();
            if (jsbState >= jsb.AssetsManager.State.PREDOWNLOAD_VERSION && jsbState !== jsb.AssetsManager.State.FAIL_TO_UPDATE) {
                Logger.getInstance().warn(``);
            }

            if (!this.m_jsbAssetsManager.getLocalManifest() || !this.m_jsbAssetsManager.getLocalManifest().isLoaded()) {
                Logger.getInstance().warn(" manifest ");
                return resolve({
                    error: UpdateDefine.ErrorState.LOAD_LOCAL_MANIFEST,
                });
            }

            Logger.getInstance().log(``);

            let checkState: UpdateDefine.CheckState = null;
            let failedState: UpdateDefine.ErrorState = null;
            this.m_jsbAssetsManager.setEventCallback((event: jsb.EventAssetsManager) => {
                switch (event.getEventCode()) {
                    case jsb.EventAssetsManager.ERROR_NO_LOCAL_MANIFEST: {
                        Logger.getInstance().warn(" Manifest ");
                        failedState = UpdateDefine.ErrorState.LOAD_LOCAL_MANIFEST;
                    }
                        break;
                    case jsb.EventAssetsManager.ERROR_DOWNLOAD_MANIFEST: {
                        Logger.getInstance().warn(" Manifest ");
                        Logger.getInstance().warn(event.getAssetsManagerEx().getLocalManifest().getManifestFileUrl());
                        failedState = UpdateDefine.ErrorState.DOWNLOAD_MANIFEST;
                    }
                        break;
                    case jsb.EventAssetsManager.ERROR_PARSE_MANIFEST: {
                        Logger.getInstance().warn(" Manifest ");
                        failedState = UpdateDefine.ErrorState.PARSE_MANIFEST;
                    }
                        break;
                    case jsb.EventAssetsManager.ALREADY_UP_TO_DATE: {
                        Logger.getInstance().log("");
                        checkState = UpdateDefine.CheckState.NOT;
                    }
                        break;
                    case jsb.EventAssetsManager.NEW_VERSION_FOUND: {
                        Logger.getInstance().log("");
                        switch (this.m_diffVersionNum) {
                            case UpdateDefine.VersionNumber.X:
                                checkState = UpdateDefine.CheckState.URL;
                                break;
                            case UpdateDefine.VersionNumber.Y:
                            case UpdateDefine.VersionNumber.Z:
                                checkState = UpdateDefine.CheckState.QUIET;
                                if (cc.sys.getNetworkType() === cc.sys.NetworkType.WWAN) {
                                    checkState = UpdateDefine.CheckState.PROMPT;
                                }
                                break;
                            default:
                                Logger.getInstance().warn("");
                                break;
                        }
                    }
                        break;
                    default:
                        break;
                }

                if (checkState !== null) {
                    this.m_jsbAssetsManager.setEventCallback(null);
                    return resolve({
                        state: checkState,
                        downloadBytes: event.getTotalBytes(),
                    });
                }

                if (failedState !== null) {
                    this.m_jsbAssetsManager.setEventCallback(null);
                    this.m_errorState = failedState;
                    return resolve({
                        error: failedState,
                    });
                }
            });

            this.m_jsbAssetsManager.checkUpdate();
        })
    }

    /**
     * 
     * @return {Promise<UpdateInterface.UpdateResult>}
     */
    public async update(): Promise<UpdateInterface.UpdateResult> {
        return new Promise((resolve: (result: UpdateInterface.UpdateResult) => void) => {

            let jsbState: jsb.AssetsManager.State = this.m_jsbAssetsManager.getState();
            if (jsbState > jsb.AssetsManager.State.READY_TO_UPDATE && jsbState !== jsb.AssetsManager.State.FAIL_TO_UPDATE) {
                Logger.getInstance().warn(``);
            }

            Logger.getInstance().log("");

            let failedCount: number = 0;
            let finishState: UpdateDefine.UpdateState = null;
            let failedState: UpdateDefine.ErrorState = null;
            let percent: number = this.m_percent;
            this.m_jsbAssetsManager.setEventCallback((event: jsb.EventAssetsManager) => {
                let eventCode: number = event.getEventCode();
                switch (eventCode) {
                    //  manifest 
                    case jsb.EventAssetsManager.ERROR_NO_LOCAL_MANIFEST: {
                        Logger.getInstance().warn(" Manifest ");
                        failedState = UpdateDefine.ErrorState.LOAD_LOCAL_MANIFEST;
                    }
                        break;
                    //  manifest 
                    case jsb.EventAssetsManager.ERROR_DOWNLOAD_MANIFEST: {
                        Logger.getInstance().warn(" Manifest ");
                        failedState = UpdateDefine.ErrorState.DOWNLOAD_MANIFEST;
                    }
                        break;
                    //  manifest 
                    case jsb.EventAssetsManager.ERROR_PARSE_MANIFEST: {
                        Logger.getInstance().warn(" Manifest ");
                        failedState = UpdateDefine.ErrorState.PARSE_MANIFEST;
                    }
                        break;
                    // 
                    case jsb.EventAssetsManager.ERROR_DECOMPRESS: {
                        Logger.getInstance().log(`${event.getAssetId()}`);
                        this.m_failedFiles.push(event.getAssetId());
                        if (failedCount++ >= MAX_FAILED_FILE_COUNT) {
                            failedState = UpdateDefine.ErrorState.DECOMPRESS_FILE;
                        }
                    }
                        break;
                    // 
                    case jsb.EventAssetsManager.UPDATE_PROGRESSION:
                        break;
                    // 
                    case jsb.EventAssetsManager.ASSET_UPDATED: {
                        percent = Util.toFixed(event.getPercentByFile() * 100);
                        Logger.getInstance().log(`${event.getAssetId()} ${percent}%`);
                    }
                        break;
                    // 
                    case jsb.EventAssetsManager.ERROR_UPDATING: {
                        Logger.getInstance().warn(`${event.getAssetId()}`);
                        this.m_failedFiles.push(event.getAssetId());
                        if (++failedCount >= MAX_FAILED_FILE_COUNT) {
                            failedState = UpdateDefine.ErrorState.DOWNLOAD_FILE;
                        }
                    }
                        break;
                    // 
                    case jsb.EventAssetsManager.UPDATE_FAILED:
                        Logger.getInstance().log(`${event.getAssetId()}`);
                        this.m_failedFiles.push(event.getAssetId());
                        if (++failedCount >= MAX_FAILED_FILE_COUNT) {
                            failedState = UpdateDefine.ErrorState.VERIFY_FILE;
                        }
                        break;
                    // 
                    case jsb.EventAssetsManager.ALREADY_UP_TO_DATE: {
                        Logger.getInstance().log("");
                        finishState = UpdateDefine.UpdateState.ALREADY_NEW;
                    }
                        break;
                    // 
                    case jsb.EventAssetsManager.UPDATE_FINISHED: {
                        Logger.getInstance().log("");
                        this.resetSearchPath();
                        percent = 100;
                        finishState = UpdateDefine.UpdateState.UPDATE_FINISH;
                    }
                        break;
                    default:
                        Logger.getInstance().warn(` ${eventCode}`);
                        break;
                }

                if (this.m_percent < percent) {
                    this.m_percent = percent;
                    EventManager.getInstance().emit(EventDefine.UPDATE_PROGRESS, this.m_percent);
                }

                if (finishState !== null) {
                    this.m_jsbAssetsManager.setEventCallback(null);
                    this.initData();
                    resolve({
                        state: finishState,
                    });
                }

                if (failedState !== null) {
                    this.m_jsbAssetsManager.setEventCallback(null);
                    this.initData();
                    this.m_errorState = failedState;
                    resolve({
                        error: failedState,
                    });
                }
            });

            this.m_jsbAssetsManager.update();
        });
    }

    /**
     * 
     * @return {Promise<UpdateInterface.UpdateResult>}
     */
    public async retry(): Promise<UpdateInterface.UpdateResult> {
        return new Promise((resolve: (state: UpdateInterface.UpdateResult) => void) => {
            if (this.m_jsbAssetsManager.getState() !== jsb.AssetsManager.State.FAIL_TO_UPDATE) {
                Logger.getInstance().warn(``);
            }

            Logger.getInstance().log("");

            switch (this.m_errorState) {
                case UpdateDefine.ErrorState.LOAD_LOCAL_MANIFEST:
                    // TODO 
                    // 1. hot-update_temp  hot-update 
                    // 2.
                    this.cleanUpdateDir();
                    resolve({
                        state: UpdateDefine.UpdateState.AGENT,
                    });
                    break;
                case UpdateDefine.ErrorState.DOWNLOAD_MANIFEST:
                    // TODO 
                    // 1.
                    // 2.
                    resolve({
                        state: UpdateDefine.UpdateState.AGENT,
                    });
                    break;
                case UpdateDefine.ErrorState.PARSE_MANIFEST:
                    // TODO 
                    // 1.
                    // 2.
                    resolve({
                        state: UpdateDefine.UpdateState.AGENT,
                    });
                    break;
                case UpdateDefine.ErrorState.DECOMPRESS_FILE:
                case UpdateDefine.ErrorState.DOWNLOAD_FILE:
                case UpdateDefine.ErrorState.VERIFY_FILE: {
                    this.m_jsbAssetsManager.setEventCallback((event: jsb.EventAssetsManager) => {
                        let percent: number = 0;
                        let failedState: UpdateDefine.ErrorState = null;
                        let finishState: UpdateDefine.UpdateState = null;
                        switch (event.getEventCode()) {
                            // 
                            case jsb.EventAssetsManager.ASSET_UPDATED: {
                                percent = Util.toFixed(event.getPercentByFile() * 100);
                                Logger.getInstance().log(`${event.getAssetId()} ${percent}%`);
                            }
                                break;
                            // 
                            case jsb.EventAssetsManager.ERROR_UPDATING: {
                                Logger.getInstance().warn(`${event.getAssetId()}`);
                                this.m_failedFiles.push(event.getAssetId());
                                failedState = UpdateDefine.ErrorState.DOWNLOAD_FILE;
                            }
                                break;
                            // 
                            case jsb.EventAssetsManager.ERROR_DECOMPRESS: {
                                Logger.getInstance().log(`${event.getAssetId()}`);
                                this.m_failedFiles.push(event.getAssetId());
                                failedState = UpdateDefine.ErrorState.DECOMPRESS_FILE;
                            }
                            // 
                            case jsb.EventAssetsManager.UPDATE_FAILED:
                                Logger.getInstance().log(`${event.getAssetId()}`);
                                this.m_failedFiles.push(event.getAssetId());
                                failedState = UpdateDefine.ErrorState.VERIFY_FILE;
                                break;
                            // 
                            case jsb.EventAssetsManager.ALREADY_UP_TO_DATE: {
                                Logger.getInstance().log("");
                                finishState = UpdateDefine.UpdateState.ALREADY_NEW;
                            }
                                break;
                            // 
                            case jsb.EventAssetsManager.UPDATE_FINISHED: {
                                Logger.getInstance().log("");
                                this.resetSearchPath();
                                percent = 100;
                            }
                                break;
                            default:

                                break;
                        }

                        if (this.m_percent < percent) {
                            this.m_percent = percent;
                            EventManager.getInstance().emit(EventDefine.UPDATE_PROGRESS, this.m_percent);
                        }

                        if (finishState !== null && finishState !== undefined) {
                            this.m_jsbAssetsManager.setEventCallback(null);
                            this.initData();
                            resolve({
                                state: finishState,
                            });
                        }

                        if (failedState !== null && failedState !== undefined) {
                            this.m_jsbAssetsManager.setEventCallback(null);
                            this.initData();
                            this.m_errorState = failedState;
                            resolve({
                                error: failedState,
                            });
                        }
                    });
                    this.m_jsbAssetsManager.downloadFailedAssets();
                }
                    break;
            }

        });
    }

    /**
     *  app 
     * @return {string}
     */
    public getAppURL(): string {
        let appURL: string = ""
        switch (cc.sys.os) {
            case cc.sys.OS_ANDROID:
                appURL = UpdateConfig.ANDROID_APK_URL;
                break;
            case cc.sys.OS_IOS:
                appURL = UpdateConfig.IOS_IPA_URL;
                break;
            default:
                Logger.getInstance().warn(` ${cc.sys.os} `);
                break;
        }
        return appURL;
    }

    /**
     * 
     * @return {string}
     */
    public getAppStoreURL(): string {
        let appStoreURL: string = ""
        switch (cc.sys.os) {
            case cc.sys.OS_ANDROID:
                appStoreURL = UpdateConfig.GOOGLE_PLAY_STORE_URL;
                break;
            case cc.sys.OS_IOS:
                appStoreURL = UpdateConfig.APP_STORE_URL;
                break;
            default:
                Logger.getInstance().warn(` ${cc.sys.os} `);
                break;
        }
        return appStoreURL;
    }

    /**
     *  update-hot  update-hot_temp 
     * @return {boolean} 
     */
    public cleanUpdateDir(): boolean {
        return this.removeUpdateTempDir() && this.removeUpdateSearchDir();
    }

    /**
     *  update-hot 
     * @return {boolean} 
     */
    private removeUpdateSearchDir(): boolean {
        let succeed: boolean = false;
        let updateSearchPath: string = this.getUpdateSearchPath();
        if (jsb.fileUtils.isDirectoryExist(updateSearchPath)) {
            succeed = jsb.fileUtils.removeDirectory(updateSearchPath);
        }
        return succeed;
    }

    /**
     *  update-hot_temp 
     * @return {boolean} 
     */
    private removeUpdateTempDir(): boolean {
        let succeed: boolean = false;
        let updateTempPath: string = this.getUpdateTempPath();
        if (jsb.fileUtils.isDirectoryExist(updateTempPath)) {
            succeed = jsb.fileUtils.removeDirectory(updateTempPath);
        }
        return succeed;
    }

    /**
     * 
     * @return {string}
     */
    private getUpdateTempPath(): string {
        let searchTempPath: string = "";
        switch (cc.sys.os) {
            case cc.sys.OS_WINDOWS:
            case cc.sys.OS_IOS:
            case cc.sys.OS_ANDROID: {
                searchTempPath = this.getAssetsWritePath() + NATIVE_UPDATE_ASSETS_FOLDER + "_temp";
            }
                break;
            case cc.sys.OS_OSX:

                break;
            default:
                Logger.getInstance().warn(" manifest ");
                break;
        }
        return searchTempPath;
    }

    /**
     * 
     * @return {string}
     */
    private getUpdateSearchPath(): string {
        return this.getAssetsWritePath() + NATIVE_UPDATE_ASSETS_FOLDER;
    }

    /**
     *  manifest 
     * @return {string}
     */
    private getManifestPathInSearch(): string {
        let manifestPath: string = "";
        switch (cc.sys.os) {
            case cc.sys.OS_WINDOWS:
                manifestPath = this.getAssetsWritePath() + NATIVE_UPDATE_ASSETS_FOLDER + "\\" + PROJECT_MANIFEST_FILENAME;
                break;
            case cc.sys.OS_OSX:

                break;
            case cc.sys.OS_IOS:
            case cc.sys.OS_ANDROID:
                manifestPath = this.getAssetsWritePath() + PROJECT_MANIFEST_FILENAME;
                break;
            default:
                Logger.getInstance().warn(" manifest ");
                break;
        }
        return manifestPath;
    }

    /**
     *  manifest  APP 
     * @return {string}
     */
    private getManifestPathInApp(): string {
        let manifestPath: string = "";
        switch (cc.sys.os) {
            case cc.sys.OS_WINDOWS:
                manifestPath = UpdateConfig.WIN32_MANIFEST_IN_APP_PATH;
                break;
            case cc.sys.OS_OSX:

                break;
            case cc.sys.OS_ANDROID: {
                let rootPath: string = jsb.fileUtils.getDefaultResourceRootPath();
                if (rootPath) {
                    if (rootPath[0] === "@") {
                        rootPath = rootPath.slice(1, rootPath.length);
                    }
                    manifestPath = rootPath + PROJECT_MANIFEST_FILENAME;
                }
            }
                break;
            case cc.sys.OS_IOS:
                let rootPath: string = "assets/";
                manifestPath = rootPath + PROJECT_MANIFEST_FILENAME;
                break;
            default:
                Logger.getInstance().warn(" manifest ");
                break;
        }
        return manifestPath;
    }

    /**
     *  manifest 
     * @return {string}
     */
    private getLocalManifestPath(): string {
        let manifestPath: string = "";
        let searchTempPath: string = this.getUpdateTempPath();
        let manifestPathSearch: string = this.getManifestPathInSearch();
        let manifestPathApp: string = this.getManifestPathInApp();

        if (jsb.fileUtils.isDirectoryExist(searchTempPath) || !jsb.fileUtils.isFileExist(manifestPathSearch)) {
            manifestPath = manifestPathApp;
        } else {
            if (jsb.fileUtils.isFileExist(manifestPathApp)) {
                manifestPath = manifestPathApp;
            }
        }

        if (!jsb.fileUtils.isFileExist(manifestPath)) {
            manifestPath = this.getManifestPathInApp();
            if (!jsb.fileUtils.isFileExist(manifestPath)) {
                Logger.getInstance().warn(" APP   manifest ");
                manifestPath = "";
            }
        }
        return manifestPath;
    }

    /**
     * 
     * @return {string} 
     */
    private getAssetsWritePath(): string {
        let path: string = "";

        if (jsb && jsb.fileUtils) {
            path = jsb.fileUtils.getWritablePath();
        } else {
            path = "/";
        }

        return path;
    }

    /**
     * 
     * @param searchPaths {string[]} 
     * @return {string[]} 
     */
    private uniqueSearchPath(searchPaths: string[]): string[] {
        let newSearchPath: string[] = [];
        let flagPathMap: Map<string, string> = new Map();
        for (let i: number = 0; i < searchPaths.length; ++i) {
            let key: string = searchPaths[i];
            if (!flagPathMap.has(key)) {
                flagPathMap.set(key, key);
                newSearchPath.push(key);
            }
        }

        flagPathMap.clear();
        return newSearchPath;
    }


    /**
     * 
     */
    private resetSearchPath(): void {
        let searchPaths: string[] = this.uniqueSearchPath(jsb.fileUtils.getSearchPaths());
        let insertSearchPaths: string[] = this.m_jsbAssetsManager.getLocalManifest().getSearchPaths();
        for (let i: number = 0; i < insertSearchPaths.length; ++i) {
            let insertSearchPath: string = insertSearchPaths[i];
            let sameSearchPathIndex: number = searchPaths.indexOf(insertSearchPath);
            if (sameSearchPathIndex >= 0) {
                searchPaths.splice(sameSearchPathIndex, 1);
            }
        }
        Array.prototype.unshift.apply(searchPaths, insertSearchPaths);
        cc.sys.localStorage.setItem(LocalStorageDefine.LOCAL_UPDATE_SEARCH_PATH, JSON.stringify(searchPaths));
        jsb.fileUtils.setSearchPaths(searchPaths);
    }

    /**
     * 
     * @param a {string} 
     * @param b {string} 
     * @return {number}  0 a  0 b  0
     *  0 NEW_VERSION_FOUND 
     *  0 0 ALREADY_UP_TO_DATE 
     */
    private onVersionCompare(a: string, b: string): number {
        Logger.getInstance().log(`${a}`);
        Logger.getInstance().log(`${b}`);

        let aSplit: string[] = a.split(".");
        let bSplit: string[] = b.split(".");

        for (let i: number = 0; i < aSplit.length; ++i) {
            let aNum: number = Number(aSplit[i]);
            let bNum: number = Number(bSplit[i]);
            if (aNum < bNum) {
                this.m_diffVersionNum = UpdateDefine.VersionNumber[UpdateDefine.VersionNumber[i]];
                return -1;
            } else if (aNum > bNum) {
                break;
            }
        }
        return 0;
    }

    /**
     * 
     * @param path {string} 
     * @param asset {jsb.ManifestAsset} 
     * @retrun {boolean} 
     */
    private onVerifyMD5(path: string, asset: jsb.ManifestAsset): boolean {
        if (!jsb.fileUtils.isFileExist(path)) {
            return false;
        }

        let fileData: Uint8Array | string = null;

        let filePathSuffix: string = path.slice(path.lastIndexOf("."), path.length);
        if (TEXT_FILE_SUFFIX.indexOf[filePathSuffix] >= 0) {
            fileData = jsb.fileUtils.getStringFromFile(path);
        } else {
            fileData = (jsb.fileUtils as any).getDataFromFile(path);
        }
        let fileMD5: string = CodeUtil.md5(fileData);

        if (fileMD5 !== asset.md5) {
            return false;
        } else {
            return true;
        }

    }

    /**
     * 
     */
    public destroy(): void {
        this.m_jsbAssetsManager = null;
        this.m_diffVersionNum = null;
        this.m_failedFiles = null;
        this.m_errorState = null;
        this.m_percent = null;
    }
}