import HttpRequest from "../../../core/http/HttpRequest";
import * as WebAPIConfig from "../../../config/WebAPIConfig";
import ViewBase from "../../ViewBase";
import * as HttpInterface from "../../../interface/HttpInterface";
import * as HttpDefine from "../../../define/HttpDefine";
import * as ServerConfig from "../../../config/ServerConfig";
import ViewDefine from "../../../define/ViewDefine";
import LocalizationDefine from "../../../define/LocalizationDefine";
import LoginView from "./LoginView";

const { ccclass, property } = cc._decorator;

@ccclass
export default class RegisterView extends ViewBase {

    // 
    private m_isPwdPlain: boolean = null;

    @property({ type: cc.EditBox, tooltip: "" })
    private edbUsername: cc.EditBox = null;

    @property({ type: cc.EditBox, tooltip: "" })
    private edbPassword: cc.EditBox = null;

    @property({ type: cc.Label, tooltip: "" })
    private labPasswordPlain: cc.Label = null;

    protected onLoad(): void {
        this.register();
        this.initData();
        this.initView();
    }

    protected start(): void {

    }

    /**
     * 
     */
    private register(): void {

    }

    /**
     * 
     */
    private unRegister(): void {

    }

    /**
     * 
     */
    private initData(): void {
        this.m_isPwdPlain = false;
    }

    /**
     * 
     */
    private initView(): void {
        this.setPwdPlain(this.m_isPwdPlain);
    }

    /**
     * 
     * @param isPlain {boolean} 
     */
    private setPwdPlain(isPlain: boolean): void {
        let inputFlag: cc.EditBox.InputFlag;
        let strPasswordPlain: string;
        if (isPlain) {
            inputFlag = cc.EditBox.InputFlag.DEFAULT;
            strPasswordPlain = "";
        } else {
            inputFlag = cc.EditBox.InputFlag.PASSWORD;
            strPasswordPlain= "";
        }
        this.edbPassword.inputFlag = inputFlag;
        this.labPasswordPlain.string = strPasswordPlain;
        this.m_isPwdPlain = isPlain;
    }

    /**
     * 
     */
    private onClickRegister(): void {
        let requestData: WebAPILoginRequest = {
            username: this.edbUsername.string,
            password: this.edbPassword.string,
        }
        let url: string = ServerConfig.WEB_API_SERVER + WebAPIConfig.WEB_API_REGISTER_REQUEST;
        HttpRequest.post(url, JSON.stringify(requestData)).then((responseInfo: HttpInterface.ResponseInfo) => {
            if (responseInfo.state === HttpDefine.StateType.OK) {
                let responseData: WebAPIResponse = JSON.parse(responseInfo.body as string);
                if(responseData.code === 0) {
                    G.UIMgr.openTips(G.Localization.get(LocalizationDefine.REGISTER_OK));
                    let loginView: cc.Node = G.UIMgr.getView(ViewDefine.LoginView);
                    let loginScript: LoginView = loginView.getComponent("LoginView");
                    loginScript.setInfo(requestData.username, requestData.password);
                    this.onClickGoback();
                }
            }
        });
    }

    /**
     * 
     */
    private onClickGoback(): void {
        G.UIMgr.showView(ViewDefine.LoginView);
        this.close();
    }

    /**
     * 
     */
    private onClickPwdPlain(): void {
        this.setPwdPlain(!this.m_isPwdPlain);
    }

    /**
     * 
     */
    protected onDestroy(): void {
        this.unRegister();
    }

}