import Manager from "../Manager";
import Logger from "../../machine/Logger";
import * as ViewDefine from "../../../define/ViewDefine";
import ViewLayerDefine from "../../../define/ViewLayerDefine";
import { ORDER_INTERVAL } from "../../../define/ViewLayerDefine";
import TweenUtil from "../../../utils/TweenUtil";
import Loader from "../../machine/Loader";
import Util from "../../../utils/Util";
import LoadingView from "../../../ui/view/persist/LoadingView";
import ProgressView from "../../../ui/view/persist/ProgressView";
import LockTouchView from "../../../ui/view/persist/LockTouchView";
import PopupsView from "../../../ui/view/common/PopupsView";
import TipsView from "../../../ui/view/common/TipsView";
import ViewBase from "../../../ui/ViewBase";
import SceneBase from "../../../ui/SceneBase";

// 
const PRELOAD_SCENE_WAITIMG_TIME: number = 1;

export default class UIManager extends Manager implements ManagerInterface {

    private static s_instance: UIManager = null;

    //  map
    private m_viewMap: Map<ViewDefineType, cc.Node> = null;

    public static getInstance(): UIManager {
        if (this.s_instance === null) {
            this.s_instance = new UIManager();
        }
        return this.s_instance;
    }

    public static destroy(): void {
        if (this.s_instance !== null) {
            this.s_instance.destroy();
        }
        this.s_instance = null;
    }

    constructor() {
        super();

        this.m_viewMap = new Map();
    }

    /**
     * 
     * @param layer {ViewLayerDefine} 
     * @returns {cc.Node[]}
     */
    private getLayerChild(layer: ViewLayerDefine): cc.Node[] {
        let views: cc.Node[] = [];

        // 
        this.m_viewMap.forEach((value: cc.Node, key: ViewDefineType, map: Map<ViewDefineType, cc.Node>) => {
            let layerDiff: number = value.zIndex - layer;
            let isLayerInView: boolean = layerDiff >= 0 && layerDiff < ORDER_INTERVAL;
            if (isLayerInView) {
                views.push(value);
            }
        });

        // 
        views.sort((a: cc.Node, b: cc.Node) => {
            if (a.zIndex < b.zIndex) {
                return -1;
            } else {
                return 1;
            }
        });

        return views;
    }

    /**
     *  order  layer
     * @param order {number} 
     * @return {ViewLayerDefine} 
     */
    private getLayerByOrder(order: number): ViewLayerDefine {
        return Math.floor(order / ORDER_INTERVAL) * ORDER_INTERVAL;
    }

    /**
     *   +1
     * @param layer {ViewLayerDefine} 
     * @return {number}
     */
    private getLayerTopOrder(layer: ViewLayerDefine): number {
        if (layer === null || layer === undefined) {
            layer = ViewLayerDefine.UI
        }
        let order: number = layer;
        let views: cc.Node[] = this.getLayerChild(layer);
        let size: number = views.length;
        if (size <= 0) {
            return order;
        } else if (views.length > ORDER_INTERVAL) {
            Logger.getInstance().warn(`${ViewLayerDefine[ViewLayerDefine[layer]]}  ${ORDER_INTERVAL} `);
            return ORDER_INTERVAL - 1;
        }
        return views[size - 1].zIndex + 1;
    }

    /**
     *    +1
     * @param layer {ViewLayerDefine} 
     * @return {number}
     */
    private resetLayerOrder(layer: ViewLayerDefine): number {
        let order: number = layer;
        let views: cc.Node[] = this.getLayerChild(layer);

        if (views.length <= 0) {
            return order;
        } else if (views.length > ORDER_INTERVAL) {
            Logger.getInstance().warn(`${ViewLayerDefine[ViewLayerDefine[layer]]}  ${ORDER_INTERVAL} `);
            return ORDER_INTERVAL - 1;
        }

        for (let i: number = 0; i < views.length; ++i) {
            views[i].zIndex = order++;
        }

        return order;
    }

    /**
     * 
     * @param layer {ViewLayerDefine} 
     * @param order {number} 
     * @return {boolean}
     */
    private checkOrderBounds(layer: ViewLayerDefine, order: number): boolean {
        return (order - layer) >= ORDER_INTERVAL;
    }

    /**
     * 
     * @return {cc.Node}
     */
    private getRootNode(): cc.Node {
        let rootNode: cc.Node = cc.director.getScene().getChildByName("Canvas");
        return rootNode;
    }

    /**
     * 
     * @param path {ViewDefineType} 
     */
    private isPersistPath(path: ViewDefineType): boolean {
        let is: boolean = false;
        for (let value in ViewDefine.PersistViewDefine) {
            if (ViewDefine.PersistViewDefine[value] === path) {
                is = true;
                break;
            }
        }
        return is;
    }

    /**
     * 
     * @param viewPath {ViewDefine.PersistViewDefine} 
     * @param viewNode {cc.Node} 
     */
    public setPersistView(viewPath: ViewDefine.PersistViewDefine, viewNode: cc.Node): void {
        let script: ViewBase = viewNode.getComponent(viewNode.name);
        if (script && script.path) {
            script.path = viewPath;
        }
        this.m_viewMap.set(viewPath, viewNode);
    }

    /**
     * 
     */
    private async loadView(path: ViewDefineType): Promise<cc.Node> {
        return new Promise((resolve: (node: cc.Node) => void, reject: () => void) => {
            let preloadTimer: NodeJS.Timeout = null;
            let startPreloadTimer: Function = () => {
                if (preloadTimer === null || preloadTimer === undefined) {
                    preloadTimer = setTimeout(() => {
                        this.openProgress();
                    }, PRELOAD_SCENE_WAITIMG_TIME * 1000);
                }
            }
            let stopPreloadTimer: Function = () => {
                if (preloadTimer !== null && preloadTimer !== undefined) {
                    this.closeProgress();
                    clearTimeout(preloadTimer);
                    preloadTimer = null;
                }
            }

            startPreloadTimer();
            let view: cc.Node = this.m_viewMap.get(path);
            if (view) {
                stopPreloadTimer();
                resolve(view);
            } else {
                Loader.getInstance().load(path, (prefab: cc.Prefab) => {
                    if (prefab) {
                        view = cc.instantiate(prefab);
                        this.m_viewMap.set(path, view);
                        stopPreloadTimer();
                        resolve(view);
                    } else {
                        stopPreloadTimer();
                        reject();
                    }
                }, (percent: number) => {
                    this.setProgress(percent);
                });
            }
        });
    }

    /**
     * 
     * @param content {string} 
     */
    public openLoading(content?: string): void {
        let loadingView: cc.Node = this.m_viewMap.get(ViewDefine.PersistViewDefine.LoadingView);
        if (loadingView) {
            let script: LoadingView = loadingView.getComponent("LoadingView");
            script.open(content);
        } else {
            Logger.getInstance().warn("LoadingView ");
        }
    }

    /**
     * 
     */
    public closeLoading(): void {
        let loadingView: cc.Node = this.m_viewMap.get(ViewDefine.PersistViewDefine.LoadingView);
        if (loadingView) {
            let script: LoadingView = loadingView.getComponent("LoadingView");
            script.close();
        } else {
            Logger.getInstance().warn("LoadingView ");
        }
    }

    /**
     * 
     */
    public openProgress(): void {
        let progressView: cc.Node = this.m_viewMap.get(ViewDefine.PersistViewDefine.ProgressView);
        if (progressView) {
            let script: ProgressView = progressView.getComponent("ProgressView");
            script.open();
        } else {
            Logger.getInstance().warn("ProgressView ");
        }
    }

    /**
     * 
     * @param percent {number}  0-100
     */
    public setProgress(percent: number): void {
        let progressView: cc.Node = this.m_viewMap.get(ViewDefine.PersistViewDefine.ProgressView);
        if (progressView) {
            let script: ProgressView = progressView.getComponent("ProgressView");
            script.setPercent(percent);
        } else {
            Logger.getInstance().warn("ProgressView ");
        }
    }

    /**
     * 
     */
    public closeProgress(): void {
        let progressView: cc.Node = this.m_viewMap.get(ViewDefine.PersistViewDefine.ProgressView);
        if (progressView) {
            let script: ProgressView = progressView.getComponent("ProgressView");
            script.close();
        } else {
            Logger.getInstance().warn("ProgressView ");
        }
    }

    /**
     * 
     */
    public openLockTouch(): void {
        let lockTouchView: cc.Node = this.m_viewMap.get(ViewDefine.PersistViewDefine.LockTouchView);
        if (lockTouchView) {
            let script: LockTouchView = lockTouchView.getComponent("LockTouchView");
            script.open();
        } else {
            Logger.getInstance().warn("LockTouchView ");
        }
    }

    /**
     * 
     */
    public closeLockTouch(): void {
        let lockTouchView: cc.Node = this.m_viewMap.get(ViewDefine.PersistViewDefine.LockTouchView);
        if (lockTouchView) {
            let script: LockTouchView = lockTouchView.getComponent("LockTouchView");
            script.close();
        } else {
            Logger.getInstance().warn("LockTouchView ");
        }
    }

    /**
     * 
     * @param content {string} 
     * @param title {string} 
     * @param parent {cc.Node}  POPUP 
     * @param confirmCallback {Function} 
     * @param cancelCallback {Function} 
     */
    public openPopups(content: string, title?: string, parent?: cc.Node, confirmCallback?: Function, cancelCallback?: Function): void {
        this.openLockTouch();
        Loader.getInstance().load(ViewDefine.CommonViewDefine.PopupsView, (prefab: cc.Prefab) => {
            if (prefab === null) {
                Logger.getInstance().warn("");
            } else {
                let layer: ViewLayerDefine = null;
                if (parent === undefined || parent === null) {
                    parent = this.getRootNode();
                    layer = ViewLayerDefine.POPUP;
                } else {
                    layer = this.getLayerByOrder(parent.zIndex);
                }

                let node: cc.Node = cc.instantiate(prefab);
                node.zIndex = this.getLayerTopOrder(layer);
                node.parent = parent;
                let script: PopupsView = node.getComponent("PopupsView");
                script.open(content, title, confirmCallback, cancelCallback);
            }
            this.closeLockTouch();
        });
    }

    /**
     * 
     * @param content {string} 
     */
    public openTips(content: string): void {
        Loader.getInstance().load(ViewDefine.CommonViewDefine.TipsView, (prefab: cc.Prefab) => {
            if (prefab === null) {
                Logger.getInstance().warn("");
            } else {
                let node: cc.Node = cc.instantiate(prefab);
                node.zIndex = this.getLayerTopOrder(ViewLayerDefine.TOP);
                node.parent = this.getRootNode();
                let script: TipsView = node.getComponent("TipsView");
                script.open(content);
            }
        });
    }

    /**
     * 
     * @param inputBox {cc.Label | cc.RichText} 
     * @param decimal {boolean} 
     * @param parent {cc.Node}  UI 
     */
    public openNumKeyboard(inputBox: cc.Label | cc.RichText, decimal?: boolean, parent?: cc.Node): void {

    }

    /**
     * 
     */
    public closeNumKeyboard(): void {

    }

    /**
     * 
     * @param content {string} 
     */
    public openRollNotice(content: string): void {

    }

    /**
     * 
     * @param name {SceneDefineType} 
     * @param completeCallback {(scene: cc.Scene) => void} 
     * @param progressCallback {(percent: number) => void}  2 
     */
    private replaceScene<T>(name: SceneDefineType, data: T, completeCallback?: (error: Error, scene: cc.Scene) => void, progressCallback?: (percent: number) => void): void {
        cc.director.preloadScene(name, (completedCount: number, totalCount: number, item: any) => {
            if (progressCallback) {
                progressCallback(Util.toFixed((completedCount / totalCount) * 100));
            }
        }, (error: Error) => {
            if (!error) {
                cc.director.loadScene(name, (error: Error, scene: cc.Scene) => {
                    if (!error) {
                        if (data !== undefined && data !== null) {
                            let script: SceneBase = scene.getChildByName("Canvas").getComponent(scene.name);
                            if (script) {
                                script.data = data;
                            } else {
                                Logger.getInstance().warn(`${name}  ${name} `);
                            }
                        }
                    } else {
                        Logger.getInstance().warn(` ${name} `, error);
                    }
                    if (completeCallback) {
                        completeCallback(error, scene);
                    }
                });
            } else {
                Logger.getInstance().warn(` ${name} `, error);
                if (completeCallback) {
                    completeCallback(error, null);
                }
            }
        });
    }

    /**
     * 
     * @param name {SceneDefineType} 
     * @param data {T} 
     * @param progressCallback {(percent: number) => void} 
     * @param completeCallback {(error: Error, scene: cc.Scene) => void,} 
     * @param preload {AssetsPathDefineType}   
     */
    public openScene<T>(name: SceneDefineType, data?: T, completeCallback?: (error: Error, scene: cc.Scene) => void, progressCallback?: (percent: number) => void, preload?: AssetsPathDefineType): void {
        this.openLockTouch();
        let preloadTimer: NodeJS.Timeout = setTimeout(() => {
            this.openProgress();
        }, PRELOAD_SCENE_WAITIMG_TIME * 1000);

        let preloadTotal: number = 0;
        if (preload !== null && preload !== undefined) {
            if (preload instanceof Array) {
                preloadTotal = preload.length;
            } else {
                preloadTotal = 1;
            }
        }

        let done: Function = (error: Error, scene: cc.Scene) => {
            if (preloadTimer !== null && preloadTimer !== undefined) {
                clearTimeout(preloadTimer);
                preloadTimer = null;
                this.closeProgress();
            }

            if (completeCallback) {
                completeCallback(error, scene);
            }
            this.closeLockTouch();

            Logger.getInstance().log(` ${name} `)
        }

        // 
        Loader.getInstance().releaseAll(() => {
            if (preloadTotal > 0) {
                let firstHalfPercent: number = 0;
                let lastHalfPercent: number = 0;
                Loader.getInstance().preload(preload, (items: cc.AssetManager.RequestItem[]) => {
                    this.replaceScene(name, data, (error: Error, scene: cc.Scene) => {
                        done(error, scene);
                    }, (percent: number) => {
                        // 
                        lastHalfPercent = Util.toFixed(firstHalfPercent + ((percent / 100) * (100 - firstHalfPercent)));
                        this.setProgress(lastHalfPercent);
                        if (progressCallback) {
                            progressCallback(lastHalfPercent);
                        }
                    });
                }, (percent: number) => {
                    // 
                    firstHalfPercent = Util.toFixed((100 / (preloadTotal + 1) * preloadTotal) * percent / 100);
                    this.setProgress(firstHalfPercent);
                    if (progressCallback) {
                        progressCallback(firstHalfPercent);
                    }
                });
            } else {
                this.replaceScene(name, data, (error: Error, scene: cc.Scene) => {
                    done(error, scene);
                }, (percent: number) => {
                    this.setProgress(percent);
                    if (progressCallback) {
                        progressCallback(percent);
                    }
                });
            }
        })
    }

    /**
     * 
     * @param path {ViewDefineType} 
     * @return {cc.Node | null}
     */
    public getView(path: ViewDefineType): cc.Node | null {
        let view: cc.Node = this.m_viewMap.get(path);
        if (!view) {
            view = null;
        }
        return view;
    }

    /**
     * 
     * @param path {ViewDefineType} 
     * @param data {any} 
     * @param completeCallback {Function} 
     * @param layer {ViewLayerDefine} 
     * @param style {ViewStyleType} 
     */
    public openView(path: ViewDefineType, data?: any, completeCallback?: (node: cc.Node) => void, layer?: ViewLayerDefine, style?: ViewStyleType): void {
        this.openLockTouch();

        let isPersist: boolean = this.isPersistPath(path);
        if (isPersist) {
            Logger.getInstance().warn(" openView ");
            this.closeLockTouch();
            return;
        }

        this.loadView(path).then((view: cc.Node) => {
            // 
            if (view.parent === null || view.parent === undefined) {
                let rootNode: cc.Node = this.getRootNode();
                if (rootNode === null || rootNode === undefined) {
                    Logger.getInstance().warn(`${cc.director.getScene()}  Canvas`);
                    return;
                }
                view.parent = rootNode;
            }

            // 
            let script: ViewBase = view.getComponent(view.name);
            if (script) {
                script.path = path;
                if (data !== null && data !== undefined) {
                    script.data = data;
                }
            }

            // 
            let topOrder: number = this.getLayerTopOrder(layer);
            if (this.checkOrderBounds(ViewLayerDefine.SYSTEM, topOrder)) {
                topOrder = this.resetLayerOrder(ViewLayerDefine.SYSTEM);
            }
            view.zIndex = topOrder;

            if (style !== null && style !== undefined) {
                view.active = false;
                TweenUtil.playOpen(view, style, () => {
                    this.closeLockTouch();
                    if(completeCallback) {
                        completeCallback(view);
                    }
                });
            } else {
                this.closeLockTouch();
                if(completeCallback) {
                    completeCallback(view);
                }
            }
        }).catch(() => {
            Logger.getInstance().warn(` ${path} `);
            this.closeLockTouch();
        });
    }

    /**
     * 
     * @param path {ViewDefineType} 
     * @param completeCallback {Function} 
     * @param style {ViewStyleType} 
     */
    public closeView(path: ViewDefineType, completeCallback?: Function, style?: ViewStyleType): void {
        this.openLockTouch();
        let view: cc.Node = this.m_viewMap.get(path);

        let done: Function = () => {
            view.destroy();
            view = null;
            if (completeCallback) {
                completeCallback();
            }
            this.m_viewMap.delete(path);

            // Loader.getInstance().unload(path);

            this.closeLockTouch();
        }

        if (view) {
            if (style !== null && style !== undefined) {
                TweenUtil.playClose(view, style, () => {
                    done();
                });
            } else {
                done();
            }
        }
    }

    /**
     * 
     * @param path {ViewDefineType} 
     * @param completeCallback {Function} 
     * @param isAbove {boolean} 
     */
    public showView(path: ViewDefineType, completeCallback?: Function, isAbove?: boolean): void {
        let view: cc.Node = this.m_viewMap.get(path);
        if (view && view.isValid) {
            if (isAbove) {
                let layer: ViewLayerDefine = this.getLayerByOrder(view.zIndex);
                let topOrder: number = this.getLayerTopOrder(layer);
                if (this.checkOrderBounds(layer, topOrder)) {
                    topOrder = this.resetLayerOrder(layer);
                }
                view.zIndex = topOrder;
            }
            view.active = true;
        }
        if (completeCallback) {
            completeCallback();
        }
    }

    /**
     * 
     */
    public hideView(path: ViewDefineType, completeCallback?: Function): void {
        let view: cc.Node = this.m_viewMap.get(path);
        if (view && view.isValid) {
            view.active = false;
        }
        if (completeCallback) {
            completeCallback();
        }
    }

    /**
     * 
     */
    public destroy(): void {
        this.m_viewMap.forEach((value: cc.Node, key: ViewDefineType) => {
            if (value.isValid) {
                value.destroy();
            }
            Loader.getInstance().release(key);
        });
        this.m_viewMap.clear();
    }

}