const { ccclass, property } = cc._decorator;

// 
enum Order {
    // 
    BELOW = 100,
    // 
    ABOVE = 101
}

@ccclass
export default class LoadingClip extends cc.Component {

    @property(cc.Sprite)
    private imgBall1: cc.Sprite = null;

    @property(cc.Sprite)
    private imgBall2: cc.Sprite = null;

    // 
    private m_anim: cc.Animation = null;

    protected onLoad(): void {
        this.initData();
        this.initView();
    }

    protected start(): void {

    }

    /**
     * 
     */
    private initData(): void {
        this.m_anim = this.node.getComponent(cc.Animation)
    }

    /**
     * 
     */
    private initView(): void {
        this.imgBall1.node.zIndex = Order.BELOW;
        this.imgBall2.node.zIndex = Order.ABOVE;
    }

    /**
     *  
     */
    private onSwitchOrder(): void {
        this.imgBall1.node.zIndex = this.imgBall1.node.zIndex === Order.BELOW ? Order.ABOVE : Order.BELOW;
        this.imgBall2.node.zIndex = this.imgBall2.node.zIndex === Order.BELOW ? Order.ABOVE : Order.BELOW;
    }

    /**
     * 
     */
    public play(): void {
        if (this.m_anim) {
            this.m_anim.play();
        }
    }

    /**
     * 
     */
    public stop(): void {
        if (this.m_anim) {
            this.m_anim.stop();
        }
        this.initView();
    }

}