import ViewBase from "../../ViewBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class UpdateView extends ViewBase {

    @property(cc.Label)
    private labTips: cc.Label = null;

    protected onLoad(): void {
        this.register();
        this.initView();
    }

    protected start(): void {

    }

    /**
     * 
     */
    private register(): void {
        G.EventMgr.on(EventDefine.UPDATE_PROGRESS, this, this.onUpdateProgress);
    }

    /**
     * 
     */
    private initView(): void {
        this.labTips.string = "";
    }

    /**
     * 
     */
    private onUpdateProgress(percent: number): void {
        this.labTips.string = percent.toString();
    }
}