import ViewBase from "../../ViewBase";

const { ccclass, property } = cc._decorator;

// 
const MOVE_TIME: number = 0.5;
// 
const STAY_TIME: number = 2;
// 
const FADE_IN_TIME: number = 0.1;
// 
const FADE_OUT_TIME: number = 0.5;
// 
const CASE_PADDING: number = 15;

@ccclass
export default class TipsView extends ViewBase {

    @property({ type: cc.Node, tooltip: "" })
    private nodCase: cc.Node = null;

    @property({ type: cc.Label, tooltip: "" })
    private labContent: cc.Label = null;

    protected onLoad(): void {
        this.initData();
        this.initView();
    }

    protected start(): void {

    }

    private initData(): void {

    }

    private initView(): void {
        this.hide();
        this.labContent.string = "";
    }

    private playAnimation(): void {
        this.nodCase.opacity = 0;
        cc.tween(this.nodCase)
            .to(FADE_IN_TIME, { opacity: 255 })
            .to(STAY_TIME, {})
            .to(FADE_OUT_TIME, { opacity: 0 })
            .call(() => {
                this.close();
            })
            .start();
    }

    /**
     * 
     * @param content {string} 
     */
    public open(content: string): void {
        this.labContent.string = content;
        (this.labContent as any)._forceUpdateRenderData();
        if (this.labContent.node.width >= this.nodCase.width) {
            this.labContent.node.width = this.nodCase.width - (CASE_PADDING * 2);
            this.labContent.overflow = cc.Label.Overflow.RESIZE_HEIGHT;
            (this.labContent as any)._forceUpdateRenderData();
        } else {
            this.nodCase.width = this.labContent.node.width + (CASE_PADDING * 2);
        }
        this.nodCase.height = this.labContent.node.height + (CASE_PADDING * 2);
        this.show();
        this.playAnimation();
    }

    /**
     * 
     */
    protected close(): void {
        if (this.node && this.node.isValid) {
            this.node.destroy();
        }
    }

}
