import Loader from "../../core/machine/Loader";
import ViewLayerDefine from "../../define/ViewLayerDefine";
import LanguagePathDefine from "../../define/LanguagePathDefine";
import ViewDefine, { PersistViewDefine } from "../../define/ViewDefine";
import UIManager from "../../core/manager/ui/UIManager";
import * as UpdateDefine from "../../define/UpdateDefine";
import * as UpdateInterface from "../../interface/UpdateInterface";
import LocalizationDefine from "../../define/LocalizationDefine";
import Util from "../../utils/Util";
import Logger from "../../core/machine/Logger";
import LanguageUtil from "../../utils/LanguageUtil";
import LocalStorageDefine from "../../define/LocalStorageDefine";
import * as GameConfig from "../../config/GameConfig";
import SceneBase from "../SceneBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class BootScene extends SceneBase implements SceneInterface {

    @property({ type: cc.Label, tooltip: "" })
    private labTips: cc.Label = null;

    @property({ type: cc.JsonAsset, tooltip: "" })
    private jsnGameConfig: cc.JsonAsset = null;

    protected onLoad(): void {
        //  constructor 
        this.register();
        this.initData();
        this.initView();
    }

    protected start() {
        this.launch();
    }

    /**
     * 
     */
    private register(): void {

    }

    /**
     * 
     */
    private initData(): void {

    }

    /**
     * 
     */
    public onLanguage(): void {

    }

    /**
     * 
     */
    private initView(): void {
        this.setTips("");
    }

    /**
     * 
     * @param str {string} 
     */
    private setTips(str: string): void {
        this.labTips.string = str;
    }

    /**
     * 
     */
    private async launch(): Promise<void> {
        await this.loadLanguage();
        await this.loadDepend();
        await this.loadUpdate();
        this.intoGame();
    }

    /**
     * 
     */
    private async loadLanguage(): Promise<void> {
        return new Promise((resolve: (localization: LocalizationDefineType) => void) => {
            let language: LanguageDefineType = cc.sys.localStorage.getItem(LocalStorageDefine.LOCAL_LANGUAGE);
            let languagePath: LanguagePathDefineType = LanguagePathDefine[language];
            if (language === null || languagePath === undefined) {
                language = LanguageUtil.transOsLanguage(cc.sys.language);
                languagePath = LanguagePathDefine[language];
                cc.sys.localStorage.setItem(LocalStorageDefine.LOCAL_LANGUAGE, language);
            }

            G.Game.language = language;

            Loader.getInstance().load(languagePath, (asset: cc.JsonAsset) => {
                if (asset === null) {
                    G.Logger.error(` ${languagePath}`);
                    G.UIMgr.openPopups("", "", this.node, () => {
                        cc.game.restart();
                    })
                } else {
                    resolve(asset.json);
                }
                Loader.getInstance().unload(languagePath);
            });
        }).then((localization: LocalizationDefineType) => {
            G.Localization.data = localization;
            G.Logger.log("");
        });
    }

    /**
     * 
     */
    private async loadPersist(): Promise<void> {
        this.setTips(G.Localization.get(LocalizationDefine.LOAD_ASSETS));
        return new Promise((resolve: () => void) => {
            let res: PersistViewDefine[] = [
                PersistViewDefine.LoadingView,
                PersistViewDefine.LockTouchView,
                PersistViewDefine.ProgressView,
                PersistViewDefine.TestView,
            ];
            Loader.getInstance().load(res, (prefabs: cc.Prefab[]) => {
                if (prefabs === null) {
                    G.Logger.error("");
                    G.UIMgr.openPopups(G.Localization.get(LocalizationDefine.INITIALIZATION_FAILED), G.Localization.get(LocalizationDefine.ERROR), this.node, () => {
                        cc.game.restart();
                    })
                } else {
                    prefabs.map((prefab: cc.Prefab, index: number) => {
                        let node: cc.Node = cc.instantiate(prefab);
                        node.zIndex = ViewLayerDefine.SYSTEM;
                        cc.game.addPersistRootNode(node);
                        node.parent = cc.director.getScene();
                        UIManager.getInstance().setPersistView(res[index], node);
                    });
                    resolve();
                }
            });
        }).then(() => {
            G.Logger.log("");
        });
    }

    /**
     * 
     */
    private async loadConfig(): Promise<void> {
        return new Promise((resolve: () => void, reject: () => void) => {
            if (this.jsnGameConfig && this.jsnGameConfig.json) {
                let gameConfig: any = this.jsnGameConfig.json;
                G.Game.version = gameConfig.version;
                resolve();
            } else {
                reject();
            }
        }).then(() => {
            G.Logger.log("");
        }).catch(() => {
            G.Logger.warn("");
        });
    }

    /**
     *  SDK
     */
    private async loadSDK(): Promise<void> {
        return new Promise((resolve: () => void) => {
            resolve();
        }).then(() => {
            G.Logger.log("SDK ")
        });
    }

    /**
     * 
     */
    private async loadDepend(): Promise<void> {
        return new Promise((resolve: () => void) => {
            Promise.all([this.loadConfig(), this.loadPersist(), this.loadSDK()]).then(() => {
                resolve();
            });
        });
    }

    /**
     * 
     */
    private async loadUpdate(): Promise<void> {
        if (!cc.sys.isNative) {
            return;
        }

        return new Promise((resolve: () => void) => {
            /**
             * 
             */
            let goMaintain: Function = () => {
                G.UIMgr.openView(ViewDefine.MaintainView);
                this.initView();
            }

            /**
             * 
             * @param state 
             */
            let goError: (state: UpdateDefine.ErrorState) => void = (state: UpdateDefine.ErrorState) => {
                let error: string = "";
                switch (state) {
                    case UpdateDefine.ErrorState.LOAD_LOCAL_MANIFEST:
                    case UpdateDefine.ErrorState.DOWNLOAD_MANIFEST:
                    case UpdateDefine.ErrorState.PARSE_MANIFEST:
                    case UpdateDefine.ErrorState.DECOMPRESS_FILE:
                    case UpdateDefine.ErrorState.DOWNLOAD_FILE:
                    case UpdateDefine.ErrorState.VERIFY_FILE:
                    case UpdateDefine.ErrorState.RETRY:
                        error = G.Localization.get(LocalizationDefine.UPDATE_FAILED_RETRY);
                        break;
                    default:
                        return;
                }

                G.UIMgr.openPopups(error, G.Localization.get(LocalizationDefine.ERROR), this.node, async () => {
                    if (cc.sys.getNetworkType() === cc.sys.NetworkType.NONE) {
                        Logger.getInstance().warn("");
                        return G.UIMgr.openPopups(G.Localization.get(LocalizationDefine.NETWORK_ERROR_RETRY), null, this.node, () => {
                            goError(state);
                        }, () => {
                            goMaintain();
                        });
                    }

                    let updateResult: UpdateInterface.UpdateResult = await G.UpdateMgr.retry();
                    if (updateResult.error !== null && updateResult.error !== undefined) {
                        return goError(updateResult.error);
                    }

                    switch (updateResult.state) {
                        case UpdateDefine.UpdateState.UPDATE_FINISH:
                            cc.game.restart();
                            break;
                        case UpdateDefine.UpdateState.ALREADY_NEW:
                            resolve();
                            break;
                        case UpdateDefine.UpdateState.AGENT:
                            goCheck();
                            break;
                        default:
                            G.Logger.error("");
                            break;
                    }

                }, () => {
                    goMaintain();
                })
            }

            /**
             * 
             */
            let goUpdate: Function = async () => {
                this.setTips(G.Localization.get(LocalizationDefine.UPDATING_ASSETS));

                if (cc.sys.getNetworkType() === cc.sys.NetworkType.NONE) {
                    Logger.getInstance().warn("");
                    return G.UIMgr.openPopups(G.Localization.get(LocalizationDefine.NETWORK_ERROR_RETRY), null, this.node, () => {
                        goUpdate();
                    }, () => {
                        goMaintain();
                    });
                }
                
                G.UIMgr.openView(ViewDefine.UpdateView);

                let updateResult: UpdateInterface.UpdateResult = await G.UpdateMgr.update();
                if (updateResult.error !== null && updateResult.error !== undefined) {
                    return goError(updateResult.error);
                }

                switch (updateResult.state) {
                    case UpdateDefine.UpdateState.UPDATE_FINISH:
                        cc.game.restart();
                        break;
                    case UpdateDefine.UpdateState.ALREADY_NEW:
                        resolve();
                        break;
                    case UpdateDefine.UpdateState.AGENT:
                        goCheck();
                        break;
                    default:
                        G.Logger.error("");
                        break;
                }
            }

            /**
             * 
             */
            let goCheck: Function = async () => {
                this.setTips(G.Localization.get(LocalizationDefine.CHECK_VERSION_INFO));

                if (cc.sys.getNetworkType() === cc.sys.NetworkType.NONE) {
                    Logger.getInstance().warn("");
                    return G.UIMgr.openPopups(G.Localization.get(LocalizationDefine.NETWORK_ERROR_RETRY), null, this.node, () => {
                        goCheck();
                    }, () => {
                        goMaintain();
                    });
                }

                let checkResult: UpdateInterface.CheckResult = await G.UpdateMgr.check();

                if (checkResult.error !== null && checkResult.error !== undefined) {
                    return goError(checkResult.error);
                }

                switch (checkResult.state) {
                    case UpdateDefine.CheckState.NOT:
                        resolve();
                        break;
                    case UpdateDefine.CheckState.QUIET: {
                        goUpdate();
                    }
                        break;
                    case UpdateDefine.CheckState.PROMPT: {
                        G.UIMgr.openPopups(G.Localization.get(LocalizationDefine.TRANFFIC_DOWNLOAD, Util.bytesToFileUnit(checkResult.downloadBytes)), G.Localization.get(LocalizationDefine.UPDATE), this.node, () => {
                            goUpdate();
                        }, () => {
                            goMaintain();
                        });
                    }
                        break;
                    case UpdateDefine.CheckState.URL: {
                        this.setTips(G.Localization.get(LocalizationDefine.DOWNING_APP));
                        cc.sys.openURL(G.UpdateMgr.getAppURL());
                        goMaintain();
                    }
                        break;
                    case UpdateDefine.CheckState.STORE: {
                        this.setTips(G.Localization.get(LocalizationDefine.NEED_STORE_UPDATE));
                        G.UIMgr.openPopups(G.Localization.get(LocalizationDefine.STORE_DOWNLOAD), G.Localization.get(LocalizationDefine.UPDATE), this.node, () => {
                            cc.sys.openURL(G.UpdateMgr.getAppStoreURL());
                            goMaintain();
                        });
                    }
                        break;
                    default: {
                        G.Logger.error(` ${checkResult.state}`);
                    }
                        break;
                }
            }

            goCheck();
        })
    }

    /**
     * 
     */
    private intoGame(): void {
        G.UIMgr.openScene(GameConfig.DEFAULT_LAUNCH_SCENE);
    }

    protected onDestroy(): void {

    }
}
